.book-details-page .star-rating button {
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
  font-size: 50px; /* Adjust the font size as needed */
  position: relative;
}

.book-details-page .star-rating .on {
  position: relative; /* Use relative positioning */
  z-index: 2; /* Set a higher z-index to overlap the off state */
}

.book-details-page .star-rating .on .star-image {
  background-image: url(./pngwing.png);
  background-position: 50% 50%;
  background-size: cover;
  height: 54px;
  left: 0;
  position: absolute; /* Use absolute positioning to overlap with the text */
  top: -10px;
  width: 56px;
}

.book-details-page .star-rating .off {
  color: #ccc;
  position: relative; /* Use relative positioning */
  z-index: 1; /* Set a lower z-index */
}

.book-details-page .star-rating body {
  padding: 100px;
}

.book-details-page .star-rating .star {
  position: relative;
  top: -20px; /* Adjust the value to move the star up or down */
}